@import 'vrem-core';
@include add-comment('html');

html {
  margin: 0;
  padding: 0;
  box-sizing: $box-sizing;
  font-size: $font-size;
  // sass-lint:disable no-vendor-prefixes
  -ms-text-size-adjust: 100%;
  -webkit-text-size-adjust: 100%;
  -webkit-font-smoothing: antialiased;
}

*,
*::before,
*::after {
  box-sizing: inherit;
}
